@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);
@import url(//cdn.jsdelivr.net/gh/velopert/font-d2coding@1.2.1/d2coding.css);
.page-template main {
  background: #f1f3f5;
  min-height: calc(100vh - 15rem); }

.header {
  background: #fcc419; }
  .header .header-content {
    height: 5rem;
    width: 1400px;
    margin: 0 auto;
    padding-left: 3rem;
    padding-right: 3rem;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center; }
    @media (max-width: 1399px) {
      .header .header-content {
        width: 100%; } }
    @media (max-width: 767px) {
      .header .header-content {
        padding-left: 1rem;
        padding-right: 1rem; } }
    .header .header-content .brand {
      color: white;
      font-size: 1.5rem;
      font-weight: 600; }
    .header .header-content .login {
      margin-left: auto;
      margin-top: 0.5rem;
      margin-right: 0.5rem;
      font-weight: 600;
      font-size: 1.2rem;
      color: rgba(255, 255, 255, 0.8);
      cursor: pointer; }
      .header .header-content .login:hover {
