.form {
  background: white;
  padding: 2rem;
  position: relative;
  padding-top: 2.5rem;
  width: 40rem; }
  .form .close {
    line-height: 2rem;
    font-size: 2rem;
    position: absolute;
    right: 1rem;
    top: 0.5rem;
    cursor: pointer; }
    .form .close:hover {
      color: #868e96; }
  .form .google {
    background-repeat: no-repeat;
    vertical-align: top;
    background-image: url("http://localhost:7777/oauth_google.PNG"); }
  .form .facebook {
    background-repeat: no-repeat;
    vertical-align: top;
    background-image: url("http://localhost:7777/oauth_facebook.PNG"); }
  .form .kakao {
    background-repeat: no-repeat;
    vertical-align: top;
    background-image: url("http://localhost:7777/oauth_kakao.PNG"); }
  .form .login-btn {
    display: block;
    width: 313px;
    height: 51px;
    margin: 0 auto 9px;
    font-size: 16px;
    color: #fff;
    text-align: center;
    line-height: 52px;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.05);
    border: none;
    background-color: white; }
  .form .error {
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
