    margin-top: 0.5rem;
    border: none;
    border-bottom: 1px solid #dee2e6;
    padding: 0.25rem;
    outline: none;
    border-radius: 4px; }

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes fadeOut {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@-webkit-keyframes slideUp {
  0% {
    -webkit-transform: translateY(50vh);
            transform: translateY(50vh); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes slideUp {
  0% {
    -webkit-transform: translateY(50vh);
            transform: translateY(50vh); }
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@-webkit-keyframes slideDown {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  100% {
    -webkit-transform: translateY(50vh);
            transform: translateY(50vh); } }

@keyframes slideDown {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  100% {
    -webkit-transform: translateY(50vh);
            transform: translateY(50vh); } }

.gray-background {
  background: rgba(100, 100, 100, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 5; }
  .gray-background.enter {
    -webkit-animation: fadeIn 0.25s ease-in both;
            animation: fadeIn 0.25s ease-in both; }
  .gray-background.leave {
    -webkit-animation: fadeOut 0.25s ease-in both;
            animation: fadeOut 0.25s ease-in both; }

.modal-wrapper {
  z-index: 10;
  position: fixed;
  top: 30%;
  left: 30%;
  transfrom: translate(-50%, -50%); }
  .modal-wrapper .modal {
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.095), 0 6px 6px rgba(0, 0, 0, 0.115);
    background: white; }

body {
  margin: 0;
  box-sizing: border-box;
  font-family: "Noto Sans KR", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

* {
  box-sizing: inherit; }

a {
  text-decoration: inherit;
  color: inherit; }

